.input-login-form {

    font-size: 15px;
    color: #fff;
    line-height: 1.2;
    border: none;
    display: block;
    width: 100%;

    height: 45px;

    background-color: transparent;
    padding: 0 5px;

}

.focus-input {

    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    color: #adadad;

}

.focus-input::before{

    content: "";
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 2px;

    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;

    background: linear-gradient(to left, #21d4fd, #b721ff);
}

.focus-input::after{

    font-size: 15px;
    color: #999999;
    line-height: 1.2;

    content: attr(data-placeholder);

    display: block;
    width: 100%;
    position: absolute;

    top: 16px;
    left: 0px;

    padding-left: 5px;


    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
}

.input-login-form:focus + .focus-input::after{

    top: -15px;
}

.input-login-form:focus + .focus-input::before{

    width: 100%;

}

